                // teachers動畫特效
                .teachers {
                    position: relative;
                
                    // vertical-align: middle;
                    // transform: translateX(0);
                    // backface-visibility: hidden;
                    -moz-osx-font-smoothing: grayscale;
                    transition-duration: 0.1s;
                    transition-property: transform;
                    &:hover,:active,:focus {
                        transform: scale(1.05);
                
                    }
                    @include table{
                        &:hover,:active,:focus {
                            transform: scale(1.176);
                            right: 8.5%;
                        
                        }
                    }
                    @include pc {
                        &:hover,:active,:focus {
                            transform: scale(1.176);
                            right: 8.5%;
                            
                        }
                    }
                }
