            // community-menu 
            .community-menu {
                top: 35%;
                right: 0%;
                z-index: 900;
                ul {               
                    a {
                        display: block;
                        width: 38px;
                        height: 38px;
                        margin-bottom: 8px;
                        background-size: cover;
                        background-repeat: no-repeat;
                        background-position: center center;
                    
                        &:nth-child(1) {
                            background-image: url($bgPath+$facebook-ico);
                        }
                        &:nth-child(2) {
