    // teacher photo 
        .teachers-list {
            .card {
                max-width: 345px;
                height: 418px;
                @include table {
                    min-width: 330px;
                }
                @include pc {
                    min-width: 350px;
                }
                &:nth-of-type(1) {
                    .photo {
                        background-image: url($bgPath+$teachers-photo-1);
                    }
                }
                &:nth-of-type(2) {
                    .photo {
                        background-image: url($bgPath+$teachers-photo-2);
                    }
                }
                &:nth-of-type(3) {
                    .photo {
                        background-image: url($bgPath+$teachers-photo-3);
                    }
                }
                &:nth-of-type(4) {
                    .photo {
                        background-image: url($bgPath+$teachers-photo-4);
                    }
                }
                &:nth-of-type(even) {
                    .photo {
                        border: 4px solid $primary !important;
                        border-radius: 50%;
                    }
                }
                &:nth-of-type(odd) {
                    .photo {
                        border: 4px solid $turkish-rose !important;
                        border-radius: 50%;
                    }
                } 
            }
            .photo { 
                position: absolute;
                width: 96px;
                height: 96px;
                background-size: cover;
                background-repeat: no-repeat;
                top: -48px;
                left: 124.5px;
                @include table {
                    left: 117px;
                }
                @include pc {
                    left: 127px;
                }
            }
        }
    // teacher photo end
